.transform-section {
  background-color: #f9fafb;
  padding: 80px 0;
  display: flex;
  justify-content: center;
}

.transform-container {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1150px;
  gap: 60px;
}

.transform-image img {
    width: 500px;
    height: auto;
    border-radius: 20px;
    background: linear-gradient(90deg, #c8c7ff, #d8e3ff);
}

.transform-content {
  flex: 1;
}

.transform-content h2 {
    font-size: 3.2rem;
    color: #0d1454;
    margin-bottom: 20px;
    font-weight: 500;
}

.transform-content p {
  color: #2c2f55;
  font-size: 1.2rem;
  line-height: 1.6;
}

.contact-info {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 25px;
}

.phone-icon {
  width: 50px;
  height: 50px;
  background: #f2f3f6;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #0d1454;
  font-size: 20px;
}

.call-label {
  color: #777;
  font-size: 0.9rem;
  margin-bottom: 5px;
}

.phone-number {
  color: #0d1454;
  font-weight: 600;
  font-size: 1.1rem;
}

.schedule-btn {
  background-color: #0d1454;
  color: #fff;
  border: none;
  padding: 14px 28px;
  font-size: 1rem;
  border-radius: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all 0.3s ease;
}

.schedule-btn span {
  background: #fff;
  color: #0d1454;
  border-radius: 50%;
  padding: 5px 8px;
  font-size: 1.1rem;
}

.schedule-btn:hover {
  transform: scale(1.05);
}